import { lazy } from "react";
// import IntroContent from "../../content/IntroContent.json";
import MiddleBlockContent from "../../content/MiddleBlockContent.json";
// import AboutContent from "../../content/AboutContent.json";
// import MissionContent from "../../content/MissionContent.json";
// import ProductContent from "../../content/ProductContent.json";
import ContactContent from "../../content/ContactContent.json";
import ScrollDirectionButton from "../../components/ScrollDirectionButton";
import { Parallax } from 'react-parallax';
// import { Slant } from "../../styles/styles";
// import { TopSlant } from "../../styles/styles";
import FirstParallaxContent from "../../content/FirstParallax.json";

const VideoBackground = lazy(() => import("../../components/VideoBackground"));
const VideoBackgroundSports = lazy(() => import("../../components/VideoBackgroundSports"));

const Contact = lazy(() => import("../../components/ContactForm"));
const MiddleBlock = lazy(() => import("../../components/MiddleBlock"));
const Container = lazy(() => import("../../common/Container"));
// const ScrollToTop = lazy(() => import("../../common/ScrollToTop"));
// const ContentBlock = lazy(() => import("../../components/ContentBlock"));

const Home = () => {
  return (
    <Container>
      <ScrollDirectionButton />
      {/* <ScrollToTop /> */}
      {/* <ContentBlock
        type="right"
        title={IntroContent.title}
        content={IntroContent.text}
        button={IntroContent.button}
        icon="developer.svg"
        id="intro"
      /> */}
      <MiddleBlock
        title={MiddleBlockContent.title}
        content={MiddleBlockContent.text}
        button={MiddleBlockContent.button}
      />
      <VideoBackground
        videoSrc="/img/video/blue-pink-bg.webm"
        title={FirstParallaxContent.title}
        text={FirstParallaxContent.text}
      />
      {/* <ContentBlock
        type="left"
        title={AboutContent.title}
        content={AboutContent.text}
        section={AboutContent.section}
        icon="graphs.svg"
        id="about"
      /> */}
      <VideoBackgroundSports
        videoSrc="/img/video/sports-video.webm"
      />
      <Parallax
        style={{ width: '100vw', position: 'relative', left: '50%', right: '50%', marginLeft: '-50vw', marginRight: '-50vw' }}
        blur={{ min: -15, max: 15 }}
        bgImage={'/img/svg/parallax-music-studio.svg'}
        bgImageAlt="Film Studio"
        strength={200}
      >
        {/* <TopSlant /> */}
        <div style={{ height: '360px' }} />
        {/* <Slant /> */}
      </Parallax>
      {/* <ContentBlock
        type="right"
        title={MissionContent.title}
        content={MissionContent.text}
        icon="product-launch.svg"
        id="mission"
      />
      <ContentBlock
        type="left"
        title={ProductContent.title}
        content={ProductContent.text}
        icon="waving.svg"
        id="product"
      /> */}
      <Contact
        title={ContactContent.title}
        content={ContactContent.text}
        id="contact"
      />
    </Container>
  );
};

export default Home;
